import React from 'react'
import moment from 'moment'

import { Row, Col, Card } from 'react-bootstrap'
import Emoji from '../common/Emoji'

const TeacherInfo = ({ teacher: teacherDetails, teacherExperience }) => {

	const cardStyle = 'm-1 border border-primary1'

	const Column = props => {
		return <Col xs={12} sm={6} className="px-0">
			{props.children}
		</Col>
	}

	return (
		<Col xs={12}>
			<Row className="justify-content-center border1">
				<Col xs={12} className="text-center">
					<h4 className="custom-font">
						   
					</h4>
				</Col>
				{/* Teacher info first column */}
				<Column>
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Text>
								<strong>{teacherDetails.name}</strong> - {teacherDetails.employeeType}<br />
								{teacherDetails.specialties.map(specialty => (
									<span key={specialty.id}>{specialty.title}</span>
								))}
							</Card.Text>
							<Card.Text>
								 : {moment(teacherDetails.employmentDate).format('LL')}
							</Card.Text>
							<Card.Text>
								{/* eslint-disable-next-line */}
								    : {teacherExperience.years}  {teacherExperience.months}  {teacherExperience.days} 
							</Card.Text>
							<Card.Text>
								{/* eslint-disable-next-line */}
								 : {teacherDetails.experienceToDate.years}  {teacherDetails.experienceToDate.months}  {teacherDetails.experienceToDate.days} 
							</Card.Text>
							<Card.Text>
								: {teacherDetails.category}
							</Card.Text>
							<Card.Text>
								{teacherDetails.isAdministration ? ' ' : null }
								{teacherDetails.isRetired ? ' ' : null }
								{teacherDetails.employeeIsAStudent ? '  ' : null }
							</Card.Text>
							<Card.Text>
								 : {teacherDetails.qualification}
							</Card.Text>
							<Card.Text>
								 : {teacherDetails.teacherTitle}
							</Card.Text>
							<Card.Text>
								 : {teacherDetails.scienceDegree}
							</Card.Text>
						</Card.Body>
					</Card>
					{/* Additinal info */}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="mb-2 text-muted">
								<Emoji label="Green Book" emoji={''} />  
							</Card.Subtitle>
							<Card.Text>
								{teacherDetails.info || ''}
							</Card.Text>
						</Card.Body>
					</Card>
				</Column>

				{/* Contacts second column*/}
				<Column>
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="mb-2 text-muted">
								<Emoji label="Telephone Receiver" emoji={''} />  
							</Card.Subtitle>
							<Card.Text>
								 : <a href={`mailto:${teacherDetails.contactEmail}`}>
									{teacherDetails.contactEmail}
								</a>
							</Card.Text>
							<Card.Text>
								 : {teacherDetails.phone}
							</Card.Text>
							<Card.Text>
								{/* eslint-disable-next-line */}
								 : {teacherDetails.residence === ''
									? <Emoji label="Cityscape" emoji={''} />
									: <Emoji label="House with Garden" emoji={''} />}
									&nbsp;{teacherDetails.residence}
							</Card.Text>
						</Card.Body>
					</Card>

					{/* Personal info */}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="mb-2 text-muted">
								<Emoji label="Memo" emoji={''} />  
							</Card.Subtitle>
							<Card.Text>
								{/* eslint-disable-next-line */} {/* no gender diversity ( */}
								: {teacherDetails.gender === ''
									? <Emoji label="Man" emoji={''} />
									: <Emoji label="Woman" emoji={''} />}
									&nbsp;{teacherDetails.gender}
							</Card.Text>
							<Card.Text>
								 : {teacherDetails.maritalStatus}
							</Card.Text>
						</Card.Body>
					</Card>

					{/* Education info */}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="mb-2 text-muted">
								<Emoji label="Graduation Cap" emoji={''} /> 
							</Card.Subtitle>
							<Card.Text>
								 : {teacherDetails.university}
							</Card.Text>
							<Card.Text>
								 : {teacherDetails.educationType}
							</Card.Text>
							<Card.Text>
								- : {teacherDetails.educationDegree}
							</Card.Text>
						</Card.Body>
					</Card>
				</Column>

				<Col xs={12} sm={10} className="px-0">
					{/* Additinal info */}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="mb-2 text-muted">
								<Emoji label="Trophy" emoji={''} />{' '}
								  
							</Card.Subtitle>
							<Card.Text>
								{teacherDetails.accomplishmentsDscr || ''}
							</Card.Text>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</Col>
	)
}

const MemodTeacherInfo = React.memo(TeacherInfo)

export default MemodTeacherInfo
