import React, { useEffect, useRef } from 'react'
import { connect } from 'react-redux'
import { setNotification, setFetchingData } from '../../reducers/notificationReducer'
import pupilsService from '../../services/pupils'
import moment from 'moment'

import { Container, Row, Col, Card } from 'react-bootstrap'
import { CommonLayout } from '../views'
import { useState } from 'react'
import Emoji from '../common/Emoji'

const PupilDetails = ({ user, setFetchingData, setNotification, match }) => {

	const cardStyle = 'my-3'

	const componentIsMounted = useRef(true)
	const [pupil, setPupil] = useState({})

	useEffect(() => {
		if (user) {
			setFetchingData(true)
			pupilsService.setToken(user.token)
			pupilsService.pupilDetailsById(match.params.id)
				.then((pupil) => {
					setPupil(pupil)

				})
				.catch(error => {
					const { message } = { ...error.response.data }
					setNotification({
						message: `   ,  : ${message}`,
						variant: 'danger'
					}, 5)
				})
				.finally(() => {
					if (componentIsMounted.current) setFetchingData(false)
				})
		}

	},[user, match.params.id, setNotification, setFetchingData])

	return <CommonLayout>
		<h1 className="text-center custom-font"> </h1>
		<h2 className="text-center text-muted custom-font">{pupil.name}</h2>
		<Container>
			<Row>
				<Col lg={6} className="px-1">
					{/*Contact info*/}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="text-muted mb-2">
								<Emoji label="Magnifying Glass Tilted Right" emoji={''} />  
							</Card.Subtitle>
							<Card.Text>
								  : {moment(pupil.createdAt).format('LL')}
							</Card.Text>
							<Card.Text>
								&apos;  : {pupil.applicantName}
							</Card.Text>
							<Card.Text>
								 email: <a href={`mailto:${pupil.contactEmail}`}>{pupil.contactEmail}</a>
							</Card.Text>
							<Card.Text>
								 : {pupil.homeAddress}
							</Card.Text>
						</Card.Body>
					</Card>

					{/*Personal info*/}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="text-muted mb-2">
								<Emoji label="Memo" emoji={''} />  
							</Card.Subtitle>
							<Card.Text>
								: {pupil.gender === ''
									? <Emoji label="Man" emoji={''} />
									: <Emoji label="Woman" emoji={''} />}
									&nbsp;{pupil.gender}
							</Card.Text>
							<Card.Text>
								: {moment(pupil.dateOfBirth).fromNow().split(' ')[0]} 
							</Card.Text>
							<Card.Text>
								 : {moment(pupil.dateOfBirth).format('Do MMMM YYYY')}
							</Card.Text>
							<Card.Text>
								  ?&nbsp;
								{pupil.docsPresent
									? <Emoji label="Check Mark" emoji={''} />
									: <Emoji label="Cross Mark" emoji={''} />
								}
							</Card.Text>
							<Card.Text>
								  ?&nbsp;
								{pupil.currentlyEnrolled
									? <Emoji label="Check Mark" emoji={''} />
									: <Emoji label="Cross Mark" emoji={''} />
								}
							</Card.Text>
							<Card.Text>
								  ?&nbsp;
								{pupil.graduated
									? <Emoji label="Check Mark" emoji={''} />
									: <Emoji label="Cross Mark" emoji={''} />
								}
							</Card.Text>
							<Card.Text>
								?&nbsp;
								{pupil.suspended
									? <Emoji label="Check Mark" emoji={''} />
									: <Emoji label="Cross Mark" emoji={''} />
								}
							</Card.Text>
							<Card.Text>
								: {pupil.hasBenefit}%
							</Card.Text>
							{pupil.info
								? <Card.Text>
									<Emoji label="Pencil" emoji={''} /> {pupil.info}
								</Card.Text>
								: null
							}
						</Card.Body>
					</Card>
				</Col>

				<Col lg={6} className="px-1">
					{/*School info*/}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="text-muted mb-2">
								<Emoji label="Graduation Cap" emoji={''} /> 
							</Card.Subtitle>
							<Card.Text>
								: {pupil.mainSchool} {pupil.mainSchoolClass} 
							</Card.Text>
							<Card.Text>
								 : {pupil.artSchoolClass} 
							</Card.Text>
							{pupil.specialty
								? <Card.Text>
									: {pupil.specialty.title}
								</Card.Text>
								: null}
							{pupil.schoolClasses
								?
								<Card.Text>
									 : {pupil.schoolClasses.map(item =>
										<span className="pl-3 d-block" key={item.id}>
											{item.title}
										</span>
									)}
								</Card.Text>
								: null
							}
						</Card.Body>
					</Card>

					{/* Parents info*/}
					<Card className={cardStyle}>
						<Card.Body>
							<Card.Subtitle className="text-muted mb-2">
								<Emoji label="Family" emoji={''} /> 
							</Card.Subtitle>
							<Card.Text>
								<strong>{pupil.fathersName}</strong><br />
								{pupil.fathersPhone}<br />
								{pupil.fathersEmploymentInfo}<br />
							</Card.Text>
							<Card.Text>
								<strong>{pupil.mothersName}</strong><br />
								{pupil.mothersPhone}<br />
								{pupil.mothersEmploymentInfo}
							</Card.Text>
						</Card.Body>
					</Card>
				</Col>
			</Row>
		</Container>
	</CommonLayout>
}

const mapStateToProps = state => {
	return {
		user: state.user,
		fetchingData: state.notification.fetchingData
	}
}

const mapDispatchToProps = {
	setFetchingData,
	setNotification
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(PupilDetails)
