import React, { useState, useEffect} from 'react'
import { connect } from 'react-redux'

import { Container, Col, Row } from 'react-bootstrap'
import { Link } from 'react-router-dom'

const SchoolExplained = ({ user }) => {

	const [userData, setUserData] = useState({})

	useEffect(() => {
		user ? setUserData({ id: user.id, superUser: user.superUser }) : setUserData({})
	}, [user])

	return <Container>
		<Row className="d-flex justify-content-center">
			<Col md={9}>
				<Row>
					<Col xs={12}>
						<h3 className="pb-2 pt-3 text-center custom-font">  </h3>
					</Col>
					<Col xs={12} className="px-3 school-explained-section">
						<p className="paragraph-text-info">
							           .
						</p>
						<p>
							,   &mdash;   {userData.superUser ? <><Link to="school/teachers"></Link>, </> : null}
							<Link to="school/groups"></Link>,{' '}
							<Link to="school/pupils"></Link>  <Link to="school/teacher/payments"></Link>.
						</p>
						<p>
							         
							   .
							      (,  ),
							    ,   .
						</p>
						<p>
							           .
							   <Link to="school/groups"> </Link>    .
							  , <Link to={`school/users/${userData.id}`}>   </Link>.
						</p>
					</Col>
				</Row>
			</Col>
		</Row>
	</Container>
}

const mapStateToProps = state => {
	return {
		user: state.user
	}
}

export default connect (
	mapStateToProps,
)(SchoolExplained)
