import React, { useEffect, useRef, useState } from 'react'
import { connect } from 'react-redux'

import pupilsService from '../../services/pupils'
import { setNotification, setFetchingData } from '../../reducers/notificationReducer'
import moment from 'moment'
import html2pdf from 'html2pdf.js'

import { CommonLayout } from '../views'

// eslint-disable-next-line1
import { Container, Row, Col, Button } from 'react-bootstrap'

const PupilFormF1 = ({ user, match, setNotification, setFetchingData }) => {

	const componentIsMounted = useRef(true)
	const [f1Data, setF1Data] = useState({})

	useEffect(() => {
		setFetchingData(true)
		if (user) {
			pupilsService.setToken(user.token)
			pupilsService.pupilF1FormData(match.params.id)
				.then(data => {
					setF1Data(data)
				})
				.catch(error => {
					const { message } = { ...error.response.data }
					setNotification({
						message: `   ,  : ${message}`,
						variant: 'danger'
					}, 5)
				})
				.finally(() => {
					if (componentIsMounted.current) setFetchingData(false)
				})
		}
	}, [user, match.params.id, setNotification, setFetchingData])

	const generatePdf = () => {
		var element = document.getElementById('f1-form')
		var opt = {
			margin: 0,
			filename: `${f1Data.name} -1.pdf`,
			// image: { type: 'jpeg', quality: 0.98 },
			html2canvas: { scale: 2 },
			jsPDF: { unit: 'mm', format: 'a4', orientation: 'portrait' }
		}

		html2pdf().set(opt).from(element).save()
	}

	return <CommonLayout>
		<Container id={'f1-form'} className="f1-form pl-5 pr-4 py-3 border rounded">
			<h6 className="text-right">-1</h6>
			<Row className="f1-header">
				<Col>
					<p>
						 :<br />
						__________________________________<br />
						__________________________________<br />
						__________________________________
					</p>
				</Col>
				<Col>
					<p className="text-right">
						  &ldquo; &ldquo;<br />
						 . .<br />
						 <span className="f1-form-field-underline">
							{f1Data.applicantName}</span><br />
						 : <span className="f1-form-field-underline">
							{f1Data.homeAddress}</span><br />
					</p>
				</Col>
			</Row>

			<h3 className="mt-2 text-center"></h3>
			<Row className="justify-content-end">
				<Col className="f1-apply-text">
					<p>   : <span className="f1-form-field-underline">
						{f1Data.name}
					</span><br />
						   : <span className="f1-form-field-underline">
						{f1Data.specialty
							? f1Data.specialty.title
							: null
						}
					</span>
					</p>
					<h6 className="mt-3 text-center f1-subheader">
						  
					</h6>
					<p>
						1. , &apos;,  : <span className="f1-form-field-underline">
							{f1Data.name}
						</span></p>
					<p>
						2. , ,  : <span className="f1-form-field-underline">
							{moment(f1Data.dateOfBirth).format('LL')}
						</span></p>
					<p>
						3.    : <span className="f1-form-field-underline">
							{f1Data.mainSchool}
						</span></p>
					<p>
						4.    <span className="f1-form-field-underline">
							{f1Data.mainSchoolClass}
						</span></p>
					<p>
						5.     : <span className="f1-form-field-underline">
							{f1Data.fathersName}, {f1Data.fathersEmploymentInfo}
						</span></p>
					<p className="pl-3">
						  <span className="f1-form-field-underline">
							{f1Data.fathersPhone}
						</span></p>
					<p>
						6.     : <span className="f1-form-field-underline">
							{f1Data.mothersName}, {f1Data.mothersEmploymentInfo}
						</span></p>
					<p className="pl-3">
						  <span className="f1-form-field-underline">
							{f1Data.mothersPhone}
						</span></p>

					<h6 className="text-center f1-subheader mt-3 mb-0">  :</h6>
					<p className="f1-docs">
						1.    <br/>
						2.         
					</p>
				</Col>
				<Col xs={12}>
					<p className="pt-2 f1-personal-data-text">
						        
						 &ldquo; &ldquo;     
						      
						/   -  
						   :<br />
						       
						&apos;        ;<br />
						,    , ,
						      .
					</p>
					<p className="f1-personal-data-text">
						&apos;       
						        
						           .
					</p>
				</Col>
			</Row>

			<Row>
				<Col className="f1-obligations-text">
					<h6 className="text-center mt-2 mb-0">
						&apos;
					</h6>
					<p>
						        ()
						 ,        
						  9    ,    ,   
						    ,  ,    
						  ,         
						   .
					</p>
					<p>
						   ,   ,   10      
						   ,     
						  ,   10        
						 .
					</p>
					<p>
						    10       
						(  )     .
					</p>
				</Col>
			</Row>
			<Row className="text-center mt-3 mb-4">
				<Col>
					<p>
						___________________20___ .
					</p>
				</Col>
				<Col>
					<p>
						______________________
					</p>
				</Col>
			</Row>
		</Container>
		<Container>
			<Row className="justify-content-center">
				<Col sm={8} className="my-5">
					<Button block
						variant="outline-primary"
						onClick={ () => generatePdf() }>
						  {`${f1Data.name} -1.pdf`}
					</Button>
				</Col>
			</Row>
		</Container>
	</CommonLayout>
}

const mapStateToProps = state => {
	return {
		user: state.user,
		fetchingData: state.notification.fetchingData
	}
}

const mapDispatchToProps = {
	setFetchingData,
	setNotification
}

export default connect(
	mapStateToProps,
	mapDispatchToProps
)(PupilFormF1)
